.footer-container {
  background-color: #242424;
  padding: 3rem 0 2rem 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.footer-subscription {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin-bottom: 24px;
  padding: 24px;
  color: #fff;
}

.footer-subscription > p {
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
    'Lucida Sans', Arial, sans-serif;
}

.footer-subscription-heading {
  margin-bottom: 24px;
  font-size: 24px;
}

.footer-subscription-text {
  margin-bottom: 24px;
  font-size: 20px;
}

.footer-input {
  padding: 8px 20px;
  border-radius: 2px;
  margin-right: 10px;
  outline: none;
  border: none;
  font-size: 18px;
  margin-bottom: 16px;
  border: 1px solid #fff;
}

.footer-links {
  width: 100%;
  max-width: 1000px;
  display: flex;
  justify-content: center;
}

.footer-link-wrapper {
  display: flex;
  flex-wrap: wrap; /* Ensure items wrap to next line if needed */
}

.footer-link-items {
  display: flex;
  justify-content: center; /* Center the buttons horizontally */
  align-items: center; /* Center items vertically */
  margin: 16px;
  text-align: center;
  width: 160px;
  box-sizing: border-box;
}

.footer-link-items .btn {
  margin: 6px; /* Adjust button margins */
  padding: 10px 20px; /* Add padding to buttons */
  width: auto; /* Allow buttons to take their own width */
}

.footer-link-items .btn a {
  color: #fff;
  text-decoration: none;
}

.footer-link-items .btn a:hover {
  color: #2e2e2e;
  transition: 0.3s ease-out;
}

.footer-link-items h2 {
  margin-bottom: 16px;
  color: #fff;
}

.footer-email-form h2 {
  margin-bottom: 2rem;
}

.footer-input::placeholder {
  color: #b1b1b1;
}

/* Social Icons */
.social-icon-link {
  color: #fff;
  font-size: 24px;
}

.social-media {
  max-width: 1000px;
  width: 100%;
}

.social-media-wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 90%;
  max-width: 1000px;
  margin: 40px auto 0 auto;
}

.social-icons {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 240px;
}

.social-logo {
  color: #fff;
  justify-self: start;
  margin-left: 20px;
  cursor: pointer;
  text-decoration: none;
  font-size: 2rem;
  display: flex;
  align-items: center;
  margin-bottom: 16px;
}

.website-rights {
  color: #fff;
  margin-bottom: 16px;
}

@media screen and (max-width: 820px) {
  .footer-links {
    padding-top: 2rem;
  }

  .footer-input {
    width: 100%;
  }

  .btn {
    width: 100%;
  }

  .footer-link-wrapper {
    flex-direction: column;
  }

  .social-media-wrap {
    flex-direction: column;
  }
}

@media screen and (max-width: 768px) {
  .footer-link-items {
    width: 100%; /* Ensure items take full width on smaller screens */
  }

  .footer-link-items .btn {
    margin: 4px; /* Adjust button margins */
    padding: 8px 16px; /* Add padding to buttons */
    font-size: 16px; /* Adjust font size */
  }
}
